import React, { useState } from 'react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  Button,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemText,
  Container,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import HomeIcon from '@mui/icons-material/Home';

// Import SMU logo and Peruna images
import smuLogo from '../../theme-assets/img/SMU Logo_InformalPeruna_Horz_digitalonly_BR.png';
import perunaOutline from '../../theme-assets/img/PerunaCorrectOutlineRB.png';

// Navigation items
const navItems = [
  { title: 'Home', path: '/' },
  { title: 'Analysis 1: Century 21', path: '/analysis-century21' },
  { title: 'Analysis 2: All Neighborhoods', path: '/analysis-all-neighborhoods' },
  { title: 'Data & Methodology', path: '/data-methodology' },
  { title: 'Downloads', path: '/downloads' },
  { title: 'Kaggle Competition', path: '/kaggle-info' },
  { title: 'About', path: '/about' },
];

const Header = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const location = useLocation();

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const isActive = (path) => {
    return location.pathname === path;
  };

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Box sx={{ p: 2, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <img
          src={perunaOutline}
          alt="SMU Peruna"
          style={{ height: '60px', marginRight: '10px' }}
        />
        <Typography variant="h6" sx={{ fontFamily: 'var(--smu-serif)' }}>
          DS 6371 Project
        </Typography>
      </Box>
      <List>
        {navItems.map((item) => (
          <ListItem
            button
            component={RouterLink}
            to={item.path}
            key={item.title}
            sx={{
              backgroundColor: isActive(item.path) ? 'rgba(53, 76, 161, 0.1)' : 'transparent',
              '&:hover': {
                backgroundColor: 'rgba(53, 76, 161, 0.05)',
              },
            }}
          >
            <ListItemText 
              primary={item.title} 
              primaryTypographyProps={{ 
                fontFamily: 'var(--smu-sans-serif)',
                fontWeight: isActive(item.path) ? '700' : '400'
              }}
            />
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static" sx={{ backgroundColor: 'var(--smu-blue)' }}>
        <Container maxWidth="xl">
          <Toolbar>
            {isMobile ? (
              <>
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  edge="start"
                  onClick={handleDrawerToggle}
                  sx={{ mr: 2 }}
                >
                  <MenuIcon />
                </IconButton>
                <Box
                  component={RouterLink}
                  to="/"
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    textDecoration: 'none',
                    color: 'inherit',
                    flexGrow: 1,
                  }}
                >
                  <img
                    src={smuLogo}
                    alt="SMU Logo"
                    className="smu-logo"
                    style={{ height: '30px' }}
                  />
                </Box>
                <IconButton color="inherit" component={RouterLink} to="/">
                  <HomeIcon />
                </IconButton>
              </>
            ) : (
              <>
                <Box
                  component={RouterLink}
                  to="/"
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    textDecoration: 'none',
                    color: 'inherit',
                    flexGrow: 1,
                  }}
                >
                  <img
                    src={smuLogo}
                    alt="SMU Logo"
                    className="smu-logo"
                    style={{ height: '40px', marginRight: '10px' }}
                  />
                  <Typography variant="h6" sx={{ fontFamily: 'var(--smu-serif)', fontWeight: 600 }}>
                    DS 6371 Project: House Prices Analysis
                  </Typography>
                </Box>
                <Box sx={{ display: 'flex' }}>
                  {navItems.map((item) => (
                    item.path !== '/' && (
                      <Button
                        key={item.title}
                        component={RouterLink}
                        to={item.path}
                        sx={{
                          color: '#fff',
                          mx: 0.5,
                          backgroundColor: isActive(item.path) ? 'rgba(255, 255, 255, 0.1)' : 'transparent',
                          '&:hover': {
                            backgroundColor: 'rgba(255, 255, 255, 0.2)',
                          },
                          fontFamily: 'var(--smu-sans-serif)',
                          fontWeight: isActive(item.path) ? '700' : '400'
                        }}
                      >
                        {item.title}
                      </Button>
                    )
                  ))}
                </Box>
              </>
            )}
          </Toolbar>
        </Container>
      </AppBar>
      <Drawer
        variant="temporary"
        open={mobileOpen}
        onClose={handleDrawerToggle}
        ModalProps={{
          keepMounted: true, // Better mobile performance
        }}
        sx={{
          display: { xs: 'block', md: 'none' },
          '& .MuiDrawer-paper': { boxSizing: 'border-box', width: 240 },
        }}
      >
        {drawer}
      </Drawer>
    </Box>
  );
};

export default Header;